@import url('https://fonts.googleapis.com/css?family=Noto+Sans+TC');
/* https://fonts.google.com/specimen/Noto+Sans+TC?selection.family=Noto+Sans+TC */

.mainStyle {
    /*main shape*/
    width: 100%;
    height: 100%;

    /*main color*/
    background-color: rgba(0, 0, 0, 0.7);

    /*main content*/
    display: -webkit-inline-box;
    display: -ms-inline-flexbox;
    display: inline-flex;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
    position: relative;
    -webkit-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
}

.boxStyle {
    /*box shape*/
    width: 300px;
    height: 80px;
    border-style: none;
    border-radius: 10px;
    -webkit-box-shadow: 1px 1px 2px 0px rgb(0, 0, 0);
            box-shadow: 1px 1px 2px 0px rgb(0, 0, 0);

    /*box color*/
    background-color: rgb(216, 216, 216, 0.5);

    /*box content*/
    padding: 25px 25px 25px 25px;
    margin: 25px 25px 25px 25px;

    /*text content*/
    font-size: 0.9em;
    color: black;
    text-align: center;

    /*CSS animate*/
    -webkit-transition: all 0.2s ease-in-out;
         -o-transition: all 0.2s ease-in-out;
            transition: all 0.2s ease-in-out;
}

.boxStyle:hover {
    background-color: rgb(216, 216, 216, 1);
    -webkit-box-shadow: 2.5px 2.5px 5px 0px rgb(0, 0, 0);
            box-shadow: 2.5px 2.5px 5px 0px rgb(0, 0, 0);
}

@media screen and (max-width: 800px) {
    .mainStyle {
        /*main shape*/
        width: 100%;
        border-style: none;
        border-radius: 0px;
        border-width: 0px;
    }

    .boxStyle {
        /*box shape*/
        width: 70vw;
        border-radius: 5px;

        /*box content*/
        padding: 15px 15px 15px 15px;
        margin: 15px 15px 15px 15px;
    }
}