.BoxExtend {
    width: 100%;
    height: 100vh;
}

.searchSetup {
    width: 300px;
    height: 250px;
    padding: 10px 10px 10px 10px;
    border-width: 2px;
    border-style: none;
    border-radius: 20px;
    background-color: rgba(0, 0, 0, 0.7);
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    margin: auto;
}

.inputSetup {
    width: 200px;
    border-radius: 10px;
    background-color: rgb(108, 108, 108);
    padding: 10px 10px 10px 10px;
    -webkit-box-shadow: 1px 1px 2px 0px rgb(0, 0, 0);
            box-shadow: 1px 1px 2px 0px rgb(0, 0, 0);
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    margin: auto;
    -webkit-transition: all 0.2s ease-in-out;
    -o-transition: all 0.2s ease-in-out;
    transition: all 0.2s ease-in-out;
}

.inputSetup:hover {
    background-color: rgb(162, 162, 162);
    -webkit-box-shadow: 2px 2px 4px 0px rgb(0, 0, 0);
            box-shadow: 2px 2px 4px 0px rgb(0, 0, 0);
}

.inputSetup input {
    width: 100%;
    height: 30px;
    font-size: 1em;
    border-radius: 5px;
}

@media screen and (max-width: 800px) {
    .searchSetup {
        width: 250px;
        height: 210px;
        padding: 5px 5px 5px 5px;
        border-width: 2px;
        border-radius: 10px;
        font-size: 0.8em;
    }

    .inputSetup {
        width: 150px;
        border-radius: 5px;
        padding: 5px 5px 5px 5px;
    }
}